import { Component, ElementRef, HostListener, ViewChild } from '@angular/core';

@Component({
  selector: 'app-root',
  template: `
    <div style="display: flex; flex-direction: column;">
      <textarea #textarea rows="5" cols="33"></textarea>
      <button (click)="openFile()" style="width: 250px;">Open file</button>
      <button (click)="getRecords()" style="width: 250px;">Get records</button>
      <button (click)="getLynfaData()" style="width: 250px;">
        Get Lynfa Data
      </button>
      <button (click)="updateLynfaData()" style="width: 250px;">
        Update Lynfa Data
      </button>
      <button (click)="hidePage()" style="width: 250px;">
        Hide Angular page
      </button>
      <button (click)="closePage()" style="width: 250px;">
        Close Angular page
      </button>
    </div>
  `,
})
export class AppComponent {
  fileOpened = false;

  @ViewChild('textarea', { static: true })
  texarea: ElementRef;
  parentEmbedded: any;
  @HostListener('window:message', ['$event'])
  onMessage(event: any) {
    this.receiveMessage(event);
  }

  constructor() {}

  receiveMessage(event: MessageEvent) {
    if (!!event && !!event.data && event.data === 'start') {
      this.parentEmbedded = event.source;
      this.parentEmbedded.postMessage({ action: 'opened' }, '*');
      return;
    }
    if (
      !!event &&
      !!event.data &&
      event.data &&
      event.data.cmd === 'msg' &&
      event.data.payload
    ) {
      this.texarea.nativeElement.value = event.data.payload;
      if (event.data.payload === 'START') {
        this.fileOpened = true;
      }
      if (event.data.payload === 'END') {
        this.fileOpened = false;
      }
    }
  }
  getLynfaData() {
    this.parentEmbedded.postMessage({ cmd: 'get' }, '*');
  }
  openFile() {
    this.parentEmbedded.postMessage({ cmd: 'get' }, '*');
  }
  getRecords() {
    if (this.fileOpened) {
      while (this.fileOpened) {
        this.parentEmbedded.postMessage({ cmd: 'get' }, '*');
      }
    }
  }

  updateLynfaData() {
    this.parentEmbedded.postMessage(
      { cmd: 'update', payload: this.texarea.nativeElement.value },
      '*'
    );
  }

  hidePage() {
    this.parentEmbedded.postMessage({ cmd: 'hide' }, '*');
  }

  closePage() {
    this.parentEmbedded.postMessage({ cmd: 'close' }, '*');
  }
}
